*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.daskcenter{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;align-items: center;
}
.daskbox{
    min-width:710px;
    padding: 15px;
    box-shadow: 0px 2px 3px gray,0px -1px 2px silver;
}
.daskbox .norecode{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;align-items: center;
}

.daskbox .norecode h2{
    font-size: 21px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.daskbox .norecode i{
    font-family: cursive;
    font-weight: 550;
    font-size:15.5px;
    margin:5px;
}
.daskbox h1{
    font-size:28px;
    margin:0 0 15px 0;
    font-family: cursive;
}
.daskbox h1 span{ 
    color:rgb(255, 20, 20);
}
.daskbox table{
    height:auto;
    width: 100%;
    box-shadow: 0px 2px 3px gray,0px -1px 2px silver;
}
.daskbox table tr{
    height:45px;
    text-align: center;
    margin: 0;
    padding: 0;
}
.daskbox table thead tr{
    background-color: black;
    color:white;
font-size: 16.5px;
font-weight:550;
}
.daskbox table tbody tr td{
    font-size: 16px;
    font-weight: 550;
}
.daskbox table tbody tr:nth-child(even){
    background-color:rgb(224, 224, 224);
}
.daskbox table tbody tr:nth-child(odd){
    background-color:rgb(231, 248, 254);
}

@media only screen and (max-width:600px){

    .daskcenter{
        height: 90vh;
        width: 100%;
        background: linear-gradient(45deg,rgb(118, 118, 118),rgb(200, 200, 200),rgb(144, 144, 144));
    }
    .daskbox{
        min-width:98%;
        padding: 10px;
        background-color:white;
        box-shadow: none;
        border: 1px solid black;
    }
    .daskbox .norecode{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;align-items: center;
    }
    
    .daskbox h1{
        font-size:24px;
    }
    .daskbox table{
      height: auto;
        width: 100%;
        box-shadow: 0px 2px 3px gray,0px -1px 2px silver;
    }
    .daskbox table tr{
        height: auto;
        text-align: center;
        margin: 0;
    }
    .daskbox table thead tr{
        background-color: black;
        color:white;
        min-height: 55PX;
    font-size: 16.5px;
    font-weight:550;
    }
    .daskbox table thead tr td{
        padding:4px;
     height: 70px;
    }
    .daskbox table tbody tr td{
        font-size: 15.5px;
        font-weight: 550;
        padding:17px 4px;
        text-align: center;
    }
    
}