*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.logincenter{
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: center;align-items: center;
}
.loginbox{
    height:350px;
    background-color: white;
    width: 60%;
box-shadow:inset 1px 2px 2px rgb(158, 158, 158),inset -1px -0.5px 2px rgb(158, 158, 158);
    padding: 50px 0 20px 0%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius:4px;
}
.loginleft{
    height:auto;
    width:30%;
    border-radius: 10px; 
    display: flex;
    flex-direction: column;
    justify-content: center;align-items: center;
}
.loginleft li{
    list-style: none;
    font-size:16px;
padding: 10px 0 0 0;
font-weight: 550;
}
.loginright{
    width: 60%;
    height: 100%;
padding-left:20px;
}
.loginright h1{
    font-size:28px;
    margin:0 0 15px 0;
    font-family: cursive;
    font-weight: 850;
}
.loginright h1 span{ 
    color:rgb(255, 20, 20);
}

.loginright form{
    height:auto;
    padding:0px;
    width: 90%;
    display: flex;
    flex-direction: column;

}
.loginright form label{
    display:flex;
    height: 40px;
    margin:10px  0 5px 0;
    width:100%;
    border-bottom: 2px solid black;
}
.loginright form label i{
    width:auto;
    height: 100%;
    display: grid;
    place-items:center;
 margin: 0 10px 0 0 ;
display: flex;
text-align: center;
padding: 10px  0 0 0;
font-size: 21px;
}
.loginright form label input {
font-size: 18px;
    height: 40px;
    width:100%;
    background: transparent;
    border: 0px;
outline: none;
}
input::placeholder{
    font-size: 17px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
#imagAnimate{
    height: 80%;
    width: auto;
}
button{
    height: 35px;
    width: 180px;
    background-color:rgb(255, 0, 51);
    border: 0px solid;outline: none;
    border-radius: 2px;
    margin-top:20px;
    color: white;
    font-size: 15.5px;
transition: 0.5s;
cursor: pointer;
}
button:hover{
    background-color:rgb(191, 3, 40);
}



@media only screen and (max-width:760px){

.logincenter{
    height: 90vh;
    width: 100%;
    background: linear-gradient(45deg,rgb(81, 81, 81),silver,rgb(119, 119, 119));
}
.loginbox{
    height:85%;
    width: 90%;
    padding:0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: none;
    border: 1px solid black;
}
.loginleft{
    height:40%;
    width:100%;
    border-radius:0px; 
    display: flex;
    flex-direction: column;
    justify-content: center;align-items: center;
    padding:0 0  10px 0;
}

.loginright{
    width: 100%;
    height: 60%;
padding-left:20px;
}
.loginright h1{
    font-size:27px;
}

.loginright form{
    height:auto;
    padding:0px;
    width: 90%;
    margin-top: 30px;
}

.loginright form label i{
font-size:19px;
}
button{
    height: 35px;
    width: 170px;
}
button:hover{
    background-color:rgb(191, 3, 40);
}
}