*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.homecontainer{
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: center;align-items: center;
    position: relative;
}
/* .CLIENT/fitnessapp/src/Components/ImgFolder/college-photo (2).png */
#homeimg{
    height: 100%;
    position: absolute;
width: 100%;

}
.homeinner{
    width: auto;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;align-items: center;
    position: relative;
    z-index: 2;
    color: white;
}
.homeinner #homeh1{
letter-spacing:4px;
font-weight: 850;

}
.homeinner p:nth-child(2){
    font-size: 18px;
    font-family: sans-serif;
    margin: 50px 0 10px 0;
}
.homeinner button{
    height:37px;
    border-radius:2px;
}
.homeinner p:nth-child(4){
margin-top:7px;
font-family: cursive;
color: red;
}

@media only screen and (max-width:600px){

    .homecontainer{
        height:90vh;
        width: 100%;
overflow-x: hidden;
    }
    /* .CLIENT/fitnessapp/src/Components/ImgFolder/college-photo (2).png */
    #homeimg{
        height:100%;
        position: absolute;
    width:180%;
    }
    .homeinner{
        width: 100%;
        height: auto;
    }
    .homeinner #homeh1{
    letter-spacing:4px;
    font-weight: 850;
    }
    .homeinner p:nth-child(2){
        font-size: 18px;
        font-family: sans-serif;
        margin: 50px 0 10px 0;
        width: 90%;
        text-align: center;
    }
    .homeinner button{
        height:37px;
        border-radius:40px;
    }
    .homeinner p:nth-child(4){
    margin-top:13px;
    font-family: cursive;
    }
    
}