*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navdiv{
    width: 100%;
    min-height: 80px;
}
.navbar{
    height: 80px;
    width: 100%;
    display: flex;
    align-items: center;
    background-color:rgb(150, 149, 149);
    position: relative;
    z-index:6;
}
.navbar #cross,.navbar #burger{
    visibility: hidden;
position: relative;
color:rgb(150, 149, 149);
right:40px;
 
}

.leftNavLogo{
    margin-left:15px;
}
.leftNavLogo{
    height: 100%;
    width: auto;
    margin-right: auto;
    margin-left:70px;
    display: flex;
    align-items: center;
}
.leftNavLogo img{
    height: 50px;
    width: 50px;
    border-radius: 50%;
}
.rightNav{
    height: 100%;
    width: auto;
    margin-left: auto;
    margin-right:30px;
    display: flex;
    align-items: center;
    visibility: visible;
    position: relative;
}

.rightNav ul{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}
.rightNav ul  li{
    height:auto;
    width:auto;
    padding:10px;
    display: flex;
    align-items: center;
    margin-left:5px;
    font-size: 17.5px;
cursor: pointer;
}

@media only screen and (max-width:600px){
    .navdiv{
        width: 100%;
        min-height: 80px;
        overflow-x:hidden;
        overflow-y: visible;
    }
    .navbar{
        height:80px;
        width: 100%;
        display: flex;
        border: 1px solid silver;
        background: rgb(80, 79, 79);
        position: absolute;
    }
    .navbar #cross{
        position: absolute;
        z-index:12;
        right: 10px;
        font-size: 27px;
        color: red;
        visibility: hidden;
    }
    .navbar #burger{
        position: absolute;
        z-index:12;
        right: 10px;
        font-size: 27px;
        color: red;
    }
    .leftNavLogo{
        margin-left:15px;
    }
    .leftNavLogo img{
        height:40px;
        width:40px;
    }
    .rightNav{
        height:80px;
        transition:0.5s;
        visibility: hidden;
        overflow: hidden;
        width: 60%;
        margin-left: auto;
        margin-right:0px;
        display: flex;
        position: absolute;
        backdrop-filter:blur(4px);
       border: 0.5px solid silver;
        top:0px;
        right:0px;
        z-index:10;
    }
    
    .rightNav ul{
        width:100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding:40px 0 0 0 ;
    }
    .rightNav ul  li{
        height:auto;
        width:auto;
        padding:10px;
        display: flex;
        align-items: center;
        margin-left:5px;
        font-size: 17.5px;
    cursor: pointer;
    }
    
}