*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.homecenter{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding:50px 0 50px 0;
}
.homecenter .signup{
    height:auto;
    background-color: white;
    width: 65%;
    box-shadow: 2px 3px 2px gray,-1px -2px 1px silver;
    padding:10px 0 10px 5%;
}
