    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

} */

.homecenter .signup h1{
    font-size:28px;
    margin:0 0 15px 0;
    font-weight: 750;
    font-family: cursive;
}
.homecenter .signup h1 span{ 
    color:rgb(255, 20, 20);
}
.signup form{
    height:auto;
    padding:0px;
    width: 75%;
    display: flex;
    flex-direction: column;
}
.signup form label{
    display:flex;
    height: 40px;
    margin:10px  0 5px 0;
    width:100%;
    border-bottom: 2px solid black;
}
.signup form label i{
    width:auto;
    height: 100%;
    display: grid;
    place-items:center;
 margin: 0 10px 0 0 ;
display: flex;
text-align: center;
padding: 10px  0 0 0;
font-size: 21px;
}
.signup form label input {
font-size: 18px;
    height: 40px;
    width:100%;
    background: transparent;
    border: 0px;
outline: none;
}
input::placeholder{
    font-size: 17px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


.imgtag{
width:auto;
height: 300px;

}
.imgtag li{
    list-style: none;
    font-size: 20px;
padding: 10px 0 0 0;
}
#imagAnimate{
    height:300px;
    width:300px;
}


/* code for making thingsa responsive */

@media only screen and (max-width:760px){


    .homecenter{
        width: 100%;
     background: linear-gradient(45deg,rgb(81, 81, 81),silver,rgb(119, 119, 119));
    }
    .homecenter .signup{
        height:auto;
                width: 92%;
    box-shadow: 1px 2px 2px rgb(158, 158, 158),-0.1px -0.5px 2px rgb(158, 158, 158);
        padding:20px;
    }
    
    .homecenter .signup h1{
        font-size:25px;
        margin:0 0 15px 0;
        font-weight: 750;
        font-family:'Courier New', Courier, monospace;
    }

    .signup form{
        width: 85%;
    }
    .signup form label i{
    font-size: 19px;
    }

    
    .imgtag{
    width: 100%;
    height: 250px;
    
    }
    .imgtag li{
        list-style: none;
        font-size: 20px;
    padding: 10px 0 0 0;
    }
    #imagAnimate{
        height:300px;
        width:300px;
    }
    


}